@import url(https://fonts.googleapis.com/css2?family=Zilla+Slab+Highlight:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Zilla+Slab&display=swap);

.city{
   display: flex;
   flex-direction: column;
   justify-items: center;
   justify-content:center;
   
}

.widgets{
    display:flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    /* margin-left: 30px; */
    align-content: center;
   /* animation: scroll 10s linear 1s infinite;*/
   
}
.ufTitle{
    margin-left: 10px;
}

@media screen and (min-width: 1024px){
    .city{
        flex-direction: row;
    }
    .widgets{
        margin-left: 30px;
    }
} 

@-webkit-keyframes scroll {
    95% { top: -100%; }
    100% { top:0; }
  } 

@keyframes scroll {
    95% { top: -100%; }
    100% { top:0; }
  }
*
{
  margin: 0;
  padding: 0;
  outline: none;
}

body
{
  font-family: 'Roboto', sans-serif;
  overflow: hidden;
  background: #d22222;;
}

h1 {
    font-family: 'Zilla Slab Highlight', serif;
    color: #fff;
    font-size: 30pt;
    text-align: center;
}
.menu {
    justify-content: center;
    justify-items: center;
    text-align: center;
}

ul {
    list-style-type: none;
    display: flex;
    justify-content: center;
    justify-items: center;
    width: 50%;
    padding: 0;
    margin: 0 auto;
  }
  #main-header {
      display: flex;
      flex-direction: column;
      justify-content: center;
      justify-items: center;

  }
  .pwabutton {
      width: 100%;
      display: flex;
      justify-items: center;
      justify-content: center;

  }
html, body, #root {
    margin:0;
    padding:0;
    width: 100%;
    height: 100%;
}
body {
    display: flex;
    background-color: #a52a2a;
}
#root
{   
    -ms-scroll-snap-type: y mandatory;   
        scroll-snap-type: y mandatory;
    scroll-behavior: smooth;
    overflow: scroll;
}
.widget{
    scroll-snap-align: none;
    
}
